@import '../../../base';

.header-desc-with-price-container {
  .insurer-logo-container {
    max-width: 120px;
    display: flex;
    justify-content: center;
    align-items: center;

    .company-logo-container {
      display: flex;
      justify-content: center;
      align-items: center;

      .company-logo {
        max-width: 70px;
        max-height: 70px;
      }
    }

    @media screen and (max-width: 690px) {
      max-width: 100px;
    }
  }

  .insurer-desc-container {
    padding: 22px;
    padding-bottom: 20px;

    .insurer-desc-top {
      .insurer-name {
        font-size: 20px;
        font-weight: $font-weight-bold;
        color: $color-title;
        margin: 0;
      }
      
      .resp-price {
        display: none;
      }
    }

    .desc {
      font-size: $font-size-small;
      font-weight: $font-weight-light;
      color: $color-body;
    }

    @media screen and (max-width: 690px) {
      padding: 15px;
      
      .insurer-desc-top {
        .insurer-name {
          font-size: $font-size-smallest;
          font-weight: $font-weight-heavy;
        }

        .resp-price {
          display: block;
          position: absolute;
          top: 12px;
          right: 15px;

          .cost {
            font-size: 20px;
            font-weight: $font-weight-heavy;
            color: $color-title;
          }
      
          .unit {
            font-size: $font-size-big * 0.5;
            font-weight: $font-weight-normal;
            color: $color-title;
          }
        }
      }

      .desc {
        font-size: 10px;
        font-weight: $font-weight-normal;
        margin-top: 5px;
      }
    }
  }

  .price-container {
    flex: 0;
    padding: 31px;
    padding-bottom: 20px;

    p {
      text-align: center;
    }

    .cost {
      font-size: 44px;
      font-weight: $font-weight-heavy;
      color: $color-title;
    }

    .unit {
      font-size: $font-size-big;
      font-weight: $font-weight-normal;
      color: $color-title;
    }

    @media screen and (max-width: 690px) {
      padding: 0;
      padding-right: 10px;
      display: none;

      .cost {
        font-size: 20px;
      }

      .unit {
        font-size: $font-size-big * 0.5;
      }
    }
  }
}