@import '../../../base';

.header-desc-with-button-container {
  min-height: 125px;
  border-bottom: solid 1px $color-line;

  .insurer-logo-container {
    max-width: 120px;
    display: flex;
    justify-content: center;
    align-items: center;

    .company-logo-container {
      display: flex;
      justify-content: center;
      align-items: center;

      .company-logo {
        max-width: 70px;
        max-height: 70px;
      }
    }

    @media screen and (max-width: 690px) {
      max-width: 100px;
    }
  }

  .insurer-desc-container {
    padding: 22px;
    padding-bottom: 20px;

    .insurer-desc-top {
      .insurer-name {
        font-size: 20px;
        font-weight: $font-weight-bold;
        color: $color-title;
        margin: 0;
      }
    }

    .desc {
      font-size: $font-size-small;
      font-weight: $font-weight-light;
      color: $color-body;
    }

    @media screen and (max-width: 690px) {
      padding: 15px;
      
      .insurer-desc-top {
        .insurer-name {
          font-size: $font-size-smallest;
          font-weight: $font-weight-heavy;
        }
      }

      .desc {
        font-size: 10px;
        font-weight: $font-weight-normal;
        margin-top: 5px;
      }
    }
  }

  .header-button-container {
    display: flex;
    justify-content: center;
    align-items: center;   
    margin-right: 28px;

    @media screen and (max-width: 690px) {
      flex: 100%;
      margin-right: 0;
      margin-bottom: 20px;

      .button-medium-color {
        min-width: 200px !important;
        height: 42px !important;

        p {
          font-size: $font-size-small !important;
        }
      }
    }
  }
}